import React, { useContext } from 'react';

import { diaContext } from '../../context/Dia/diaContext';

// Components
import { Item } from './Item';

export const Dia = () => {

    // Context
    const nuevoDiaContext = useContext(diaContext);
    const { comidasDia } = nuevoDiaContext;

    // Ordenamos alfabeticamente el arreglo de comidas
    comidasDia.sort();

    if (comidasDia.length === 0) return <p className="text-center fz-15">Agregue sus comidas desde el menu "Alimentos"</p>;

    // Calorias consumidas
    const arrayCaloriasPorcion = comidasDia.map(comida => comida.gramosConsumidos / comida.porcion * comida.caloriasPorcion);
    const caloriasConsumidas = arrayCaloriasPorcion.reduce((a, b) => a + b);

    // Guardar en calendario lo comido en el dia
    const guardarDiaCalendario = () => {
        for (let i = 0; i < comidasDia.length; i++) {
            if (comidasDia[i].gramosConsumidos === 0) {
                return console.log('faltan datos')
            }
        }
        console.log('todo bien')
    };

    return (
        <div>
            <h1 className='text-center'>FORMULARIO NUEVO DIA</h1>
            <form className="form-dia">
                <div className="mb-3 form-date">
                    <label className="form-label">Fecha</label>
                    <input type="date" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                </div>
                <div className="mb-3">
                    <label className="form-label">Comidas agregadas: (Agreguelas desde el menu "Alimentos")</label>
                    <div className="row row-cols-1 row-cols-md-3 g-4">
                        {
                            comidasDia.map(comida => (
                                <Item
                                    key={comida._id}
                                    comida={comida}
                                />
                            ))
                        }
                    </div>
                </div>
                {
                    !!caloriasConsumidas === false ? <p className="text-center fz-30">TOTAL DE CALORIAS CONSUMIDAS: 0</p> : <p className="text-center fz-30">TOTAL DE CALORIAS CONSUMIDAS: {caloriasConsumidas.toFixed(2)}</p>
                }
                <div className="d-grid gap-2">
                    <button
                        type="button"
                        className="btn btn-primary"
                        onClick={guardarDiaCalendario}
                    >GUARDAR</button>
                </div>
            </form>
        </div>
    );
};